
$color-blue: #3b9ada;

.container {
	max-width: 1140px;
	margin: 0 auto; }
.main-section {
	height: 880px;
	background: #f0f0f0;
	padding-top: 16px;
	&__nav-container {
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		height: 69px;
		align-items: center; }
	&__logo {
		width: 134px; } }
.nav-menu {
	&__list {
		display: flex {
        flex-direction: row; } }
	&__list-item {
		margin-right: 64px;
		font-size: 18px;
		&:last-child {
			margin-right: 0px; } } }

.left-wrapper {
    position: relative;
    img {
        position: relative;
        z-index: 5; }
    &__rectangle-blue {
        position: absolute;
        top: 150px;
        left: -50px;
        width: 565.95px;
        height: 220px;
        background: blue;
        border-radius: 100px;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        transform: rotate(-64deg);
        z-index: 2; }
    &__rectangle-orange {
        position: absolute;
        width: 618.43px;
        height: 220px;
        left: -30px;
        top: 160px;
        background: orange;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        border-radius: 100px;
        transform: rotate(-50.81deg);
        z-index: 3; }
    &__rectangle-grey {
        position: absolute;
        width: 608.97px;
        height: 220px;
        left: 10px;
        top: 190px;
        background: #E2E2E2;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        border-radius: 100px;
        transform: rotate(-36.15deg);
        z-index: 1; } }


